.container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  position: relative;
}

.btn {
  padding: 12px 24px;
  cursor: pointer;
  position: relative;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.btn:hover {
  background-color: #0056b3;
  transform: translateY(-1px);
}

.btn:active {
  transform: translateY(0);
}

.btn:focus {
  outline: none;
}

.popover {
  position: absolute;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  padding: 1rem;
  max-width: 276px;
  z-index: 1000;
  transform: translate(-50%, -100%);
  margin-top: -10px;
  pointer-events: none;
}

.popover-header {
  padding: 0.5rem 0;
  margin: 0;
  font-size: 1rem;
  border-bottom: 1px solid #ebebeb;
}

.popover-body {
  padding: 0.5rem 0;
}

.popover::after {
  content: '';
  position: absolute;
  bottom: -9px;
  left: 50%;
  transform: translateX(-50%);
  border-width: 10px 10px 0;
  border-style: solid;
  border-color: #fff transparent transparent;
  z-index: 1;
}

.popover::before {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  border-width: 10px 10px 0;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.2) transparent transparent;
}
